<template>
  <div id="app">
    <table>
        <div>
            
            <input type="text" v-test="searchName" placeholder="Search">
        </div>
        <tr v-for="p in eventedAction" :key="p.id">
            
            <td v-text="p.name" class="name"></td>
            <td class="type" v-text="p.type1" :style="{background:typeColor(p.type1)}"></td>
            <td v-text="p.type2" class="type" :style="{background:typeColor(p.type2)}"></td>
            <td v-text="p.H"></td>
            <td v-text="p.A"></td>
            <td v-text="p.B"></td>
            <td v-text="p.C"></td>
            <td v-text="p.D"></td>
            <td v-text="p.S"></td>
            <td v-text="p.Total"></td>
            <!-- <td><img :src="p.img"></td>
            <td><img :src="p.type1"></td>
            <td><img :src="p.type2"></td> -->
        </tr>
    </table>
  </div>
</template>

<script>
import poke_data from './assets/database.json'
export default {
  name: '#app',
  data () {
    return {
      poke: poke_data,
      searchName: '',
      text: ''
    }
  },
  methods: {
    hiraToKana: function(str) {
        return str.replace(/[\u3041-\u3096]/g, function(match) {
            var chr = match.charCodeAt(0) + 0x60;
            return String.fromCharCode(chr);
        });
    },
    typeColor: function(str){
        if(str=="くさ"){return "#78BF5E";}
        else if(str=="ほのお"){return "#EBA466";}
        else if(str=="みず"){return "#6191C4";}
        else if(str=="むし"){return "#9EBF4C";}
        else if(str=="エスパー"){return "#E57C7D";}
        else if(str=="ひこう"){return "#99AEDC";}
        else if(str=="でんき"){return "#F2D45B";}
        else if(str=="ノーマル"){return "#929DA2";}
        else if(str=="はがね"){return "#668EA3";}
        else if(str=="ゴースト"){return "#5769B1";}
        else if(str=="ドラゴン"){return "#2E68C7";}
        else if(str=="フェアリー"){return "#DA94E4";}
        else if(str=="あく"){return "#5A5767";}
        else if(str=="いわ"){return "#C8B68F";}
        else if(str=="じめん"){return "#CC7D4D";}
        else if(str=="どく"){return "#9F65C2";}
        else if(str=="こおり"){return "#85D1C2";}
        else if(str=="かくとう"){return "#C7496A";}
        else{return "#ffffff"}
    }  
  },
  computed: {
      eventedAction: function() {
        let list = this.poke.slice();

        // Nameで検索実施
        if (this.searchName) {
                var s = this.hiraToKana(this.searchName)
                list = list.filter(element => {
                    return Object.keys(element).some(key => {
                        if(key === 'name') {
                            return element[key].indexOf(s) > -1;
                        }
                    });
                });
        }
        if(list.length === 0){return this.poke}
        return list;
    }
  },

  directives: {
      test: {
          bind: function (el, binding, vnode) {
            el.value = binding.value
            el.addEventListener('keyup', () => {vnode.context[binding.expression] = el.value})
            el.addEventListener('compositionend', () => {vnode.context[binding.expression] = el.value})
          },
          update: function(el, {value}){ el.value = value }
      }
  }

}
</script>

<style>
#app {
  font-family: 'PixelMplus12-Regular', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 60px;
  font-size: 3vw;
}

.type{
    background-color: #47435C;
    border-radius: 5px;
    color: #ffff;
}

.name{
    text-align:left;
}

@media screen and (max-width:480px){
  /*画面幅が480pxまでの時*/
  
.name{
    text-align:left;
    width: 80px;
    height: 25px;
}

}
@media screen and (min-width:781px){
  /*画面幅が781px以上の時*/
}

table{

  border-collapse: collapse;
  border-spacing: 0;
}



table tr:nth-child(odd){
  background-color: #eee
}

input[type="text"]:focus {
  outline: 0;
}

</style>
